// @ts-nocheck
import {
  Checkbox,
  Flex,
  HStack,
  Icon,
  Input,
  Stack,
  Switch,
  Text,
  useDisclosure,
  useToast,
} from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import DashedButton from 'app/components/DashedButton';
import InputComponent from 'app/pages/Appium/components/InputComponent';
import LaunchIconButton from 'app/pages/Appium/components/LaunchIconButton';
import SelectComponent from 'app/pages/Appium/components/SelectComponent';
import UploadButton from 'app/pages/Appium/components/UploadButton';
import LabelFieldComponent from 'app/pages/AutomationTest/components/LabelFieldComponent';
import React, { useEffect, useState } from 'react';
import { tableData } from 'app/pages/MozarkDevices';
import CustomIconButton from 'app/pages/Appium/components/CustomIconButton';
import SelectDevicePopup from 'app/pages/Appium/components/SelectDevicePopup';
import InfoComponent from 'app/pages/Appium/components/InfoComponent';
import InputDateTime from 'app/pages/Appium/components/InputDateTime';
import PopupCardDetail from 'app/pages/Appium/components/PopupCardDetail';
import { useDispatch, useSelector } from 'react-redux';
import {
  selectAppBuildAndroidListState,
  selectAppBuildListState,
  selectCreateProjectLoading,
  selectListingDevices,
  selectProjectList,
  selectScheduleExecuteTestLoading,
  selectScheduleTestResponse,
  selectScriptAndroidState,
  selectScriptState,
  selectTestExecutionSelectedDevice,
  selectUploadApplicationUrlAndroid,
  selectuploadApplicationUrlAndroidError,
  selectUploadingApplicationLoading,
  selectApplicationFileUploadLoading,
  selectUploadingApplicationAndroidLoading,
  selectUploadFileProgressValue,
  selectApplicationFileUploadResponse,
  selectCreatedProject,
  selectBusyDeviceSlots,
  selectDeviceListLoading,
  selectDeviceListingError,
  selectCreateProjectError,
  selectScheduleTestResponseError,
  selectTestExcutionState,
} from 'app/pages/Xcui/slice/selector';
import { useTestExecutionSlice } from 'app/pages/Xcui/slice';
import md5 from 'md5';
import CheckboxComp from 'app/pages/UiAutomator/components/CheckboxComp';
import setHours from 'date-fns/setHours';
import setMinutes from 'date-fns/setMinutes';
import {
  dateTimeConverterWithTimeZone,
  getMd5SumFiles,
  validateBusyDevice,
} from 'utils/gernricfunction';
import LoadingComponent from 'app/components/LoadingComponent';
import SelectValueComponent from '../components/SelectValueComponent';
import { timeIntervalValue } from 'app/pages/Xcui/Features/XcUiMozarkTesting';
import KeyValueComponent from '../components/KeyValueComponent';
import NewKeyValueComponent from '../components/NewKeyValueComponent';
import SelectTrayPopup from '../components/SelectTrayPopup';
import { selecteTrayList } from 'app/pages/MyDevices/slice/selector';
import { useMyDevicesSlice } from 'app/pages/MyDevices/slice';
import SelectTrayOption from '../components/SelectTrayOption';
import ToggleSelectInput from '../components/ToggleSelectInput';
import { useNavigate } from 'react-router-dom';
import { HiOutlineRefresh } from 'react-icons/hi';
import AppBuildMwebButton from '../components/AppBuildMwebButton';
import getApplicationUrlHelper from 'utils/getFileUrlFromList';
import getTrayFromList from 'utils/getTrayFromList';
import ROUTES from 'app/constants/Routes';
import RefreshIconButton from 'app/components/RefreshIconButton';
import useSelectedDevicesStatus from 'app/utils/useSelectedDevicesStatus';
import useDevicesLoadInterval from 'app/utils/useDevicesLoadInterval';
import usePlatformtypeHook from 'app/pages/HomePage/usePlatformtypeHook';

function UiAutomatorMozarkTesting({ language, getTabIndex, framework }) {
  const [getInterval, setInterval] = useState(5);
  const [getActiveTab, setActiveTab] = useState(0);
  const [getMaxTestDuration, setMaxTestDuration] = useState(10);
  const [startDate, setStartDate] = useState(
    setHours(
      setMinutes(new Date(), new Date().getMinutes()),
      new Date().getHours(),
    ),
  );
  const [endDate, setEndDate] = useState(
    setHours(
      setMinutes(new Date(), new Date().getMinutes()),
      new Date().getHours(),
    ),
  );
  const [getMessageFlag, setMessageFlag] = useState(false);
  const [activeTrayPopup, setActiveTrayPopup] = useState(false);
  const [getSelectedDeviceList, setSelectedDeviceList] = useState([]);
  const [getSelectedDeviceListUiautomator, setSelectedDeviceListUiautomator] =
    useState([]);
  const [recordDeviceScreen, setRecordDeviceScreen] = useState(false);
  const [recordNetworkCalls, setRecordNetworkCalls] = useState(false);
  const [systemDebugLogs, setSystemDebugLogs] = useState(true);
  const [automationExecutionLogs, setAutomationExecutionLogs] = useState(true);
  const [recordMemory, setRecordMemory] = useState(false);
  const [recordCPU, setRecordCPU] = useState(false);
  const [recordBattery, setRecordBattery] = useState(false);
  const [recordGraphics, setRecordGraphics] = useState(false);
  const [captureDataUsageMetrics, setCaptureDataUsageMetrics] = useState(false);
  const [dataUsageTimeInterval, setDataUsageTimeInterval] = useState(false);
  const [recordDeviceNetworkPackets, setRecordDeviceNetworkPackets] =
    useState(false);
  const [recordDeviceScreenShots, setRecordDeviceScreenShots] = useState(true);
  const [liveLogs, setLiveLogs] = useState(true);
  const selectProjectOptionList = useSelector(selectProjectList);
  const [projectName, setProjectName] = useState('');
  const [activeDevicePopup, setActiveDevicePopup] = useState(false);
  const selectTrayOptionListUiautomator = useSelector(selecteTrayList);
  const [selectTrayValue, setTraySelectValue] = useState();
  let history = useNavigate();
  const [
    uiAutomatorMozarkTestingPopupCard,
    setUiAutomatorMozarkTestingPopupCard,
  ] = useState(false);
  const [getAddedKey, setAddedKey] = useState([]);
  const [selectProject, setSelectProject] = useState('');
  const [getPackageName, setPackageName] = useState('');
  const [selectAppBuild, setSelectAppBuild] = useState('');
  const [selectBrowser, setSelectBrowser] = useState('');
  const [selectVersion, setSelectVersion] = useState('');
  const [getFilterDeviceType, setFilterDevicesType] = useState([]);
  const [getFilterTrayType, setFilterTrayType] = useState([]);
  const [selectScript, setSelectScript] = useState('');
  const [appbuildFile, setAppbuildFile] = useState();
  const [activePopupCard, setActivePopupCard] = useState('');
  const [getMd5Sum, setMd5Sum] = useState('');
  const [getMd5SumScript, setMd5SumScript] = useState('');
  const [getApplicationType, setApplicationType] = useState('');
  const [getSelectedTray, setSelectedTray] = useState();
  const [isVisEnabled, setVisEnable] = useState(false);
  const [getVisualUrl, setVisualUrl] = useState('');
  const [selectVisualScript, setSelectVisualScript] = useState();
  const uploadAppBuildUrlAndroid = useSelector(
    selectUploadApplicationUrlAndroid,
  );
  const [areDevicesBusy, setAreDevicesBusy] = useState(true);
  const uploadApplicationAndroidError = useSelector(
    selectuploadApplicationUrlAndroidError,
  );
  const { testExecutionActions } = useTestExecutionSlice();
  const selectAppBuildListOptions = useSelector(selectAppBuildAndroidListState);
  const selectScriptListOptions = useSelector(selectScriptAndroidState);
  const dispatch = useDispatch();
  const toast = useToast();
  const getListingDevices = useSelector(selectListingDevices);
  const selectedDevices = useSelector(selectTestExecutionSelectedDevice);
  const getScheduleTestResponse = useSelector(selectScheduleTestResponse);
  const getScheduleTestResponseError = useSelector(
    selectScheduleTestResponseError,
  );
  const loadingCreateProject = useSelector(selectCreateProjectLoading);
  const loadingFileUpload = useSelector(selectApplicationFileUploadLoading);
  const loadingapplicationUrl = useSelector(
    selectUploadingApplicationAndroidLoading,
  );
  const isMobileTesting = usePlatformtypeHook();
  const loadingExecuteSchedule = useSelector(selectScheduleExecuteTestLoading);
  const getUploadProgessValue = useSelector(selectUploadFileProgressValue);
  const fileUploadResponse = useSelector(selectApplicationFileUploadResponse);
  const createdPojectResponse = useSelector(selectCreatedProject);
  const createdPojectError = useSelector(selectCreateProjectError);
  const busyDeviceSlots = useSelector(selectBusyDeviceSlots);
  const deviceListListLoading = useSelector(selectDeviceListLoading);
  const deviceListDataError = useSelector(selectDeviceListingError);
  const { selectVisualScriptList } = useSelector(selectTestExcutionState);

  const { actions } = useMyDevicesSlice();

  const getFilterDevice = (getListingDevices, key) => {
    if (getListingDevices.length === 0) {
      return getListingDevices;
    }
    if (key === 'android') {
      const getAndroidDevicesList = getListingDevices.filter(
        device => device.platform === 'android',
      );
      return getAndroidDevicesList;
    } else if (key === 'ios') {
      const getIosDevicesList = getListingDevices.filter(
        device => device.platform === 'ios',
      );
      return getIosDevicesList;
    } else {
      const getAndroidIosDevicesList = getListingDevices.filter(
        device => device.platform === 'android' || device.platform === 'ios',
      );
      return getAndroidIosDevicesList;
    }
  };

  const getAndroidDevicesList = getListingDevices.filter(
    device =>
      device.platform === 'android' ||
      (framework === 'appium' && device.platform === 'ios'),
  );
  const selectTrayOptionList = selectTrayOptionListUiautomator.filter(
    tray =>
      tray.platform === 'android' ||
      (framework === 'appium' && tray.platform === 'ios'),
  );

  const getDevices = () => {
    if (framework === 'appium') {
      return getFilterDeviceType;
    }
    return getAndroidDevicesList;
  };
  useDevicesLoadInterval(isMobileTesting, testExecutionActions, '');
  useSelectedDevicesStatus(
    getSelectedDeviceList,
    setAreDevicesBusy,
    getDevices(),
  );

  useEffect(() => {
    setProjectName('');
    setSelectProject('');
    setSelectAppBuild('');
    setSelectScript('');
    setStartDate(
      setHours(
        setMinutes(new Date(), new Date().getMinutes()),
        new Date().getHours(),
      ),
    );
    setEndDate(
      setHours(
        setMinutes(new Date(), new Date().getMinutes()),
        new Date().getHours(),
      ),
    );
    setSelectedDeviceList([]);
    setSelectedDeviceListUiautomator([]);
    setAppbuildFile();
    setRecordDeviceScreen(false);
    setRecordNetworkCalls(false);
    setRecordMemory(false);
    setRecordCPU(false);
    setRecordBattery(false);
    setRecordGraphics(false);
    setRecordDeviceScreenShots(true);
    setAutomationExecutionLogs(true);
    setLiveLogs(true);
    setDataUsageTimeInterval(false);
    setCaptureDataUsageMetrics(false);
    setRecordDeviceNetworkPackets(false);
    setSystemDebugLogs(true);
    dispatch(testExecutionActions.uploadFileProgress(0));
    dispatch(testExecutionActions.BusyDeviceSlotsListLoaded({}));
    setMaxTestDuration(10);
    setAddedKey([]);
    dispatch(actions.trayDataLoaded([]));
    setTraySelectValue('');
  }, [getTabIndex]);

  useEffect(() => {
    if (
      fileUploadResponse.hasOwnProperty('status') &&
      fileUploadResponse.status === 200
    ) {
      if (fileUploadResponse.type === 'android-uiautomator') {
        if (getApplicationType === 'appbuild') {
          const paramStringAppBuild = {
            fileCategory:
              framework === 'appium'
                ? 'android-application,ios-application'
                : 'android-application',
            projectName: selectProject,
            testType: 'app-automation',
          };

          dispatch(
            testExecutionActions.loadSelectAppBuildAndroidList(
              paramStringAppBuild,
            ),
          );
        }

        if (
          getApplicationType === 'android-test-application' ||
          getApplicationType === `appium-${language}-test-application`
        ) {
          const paramStringScript = {
            fileCategory:
              framework === 'appium'
                ? `appium-${language}-test-application`
                : 'android-test-application',
            projectName: selectProject,
            testType: 'app-automation',
          };

          dispatch(
            testExecutionActions.loadSelectScriptAndroidList(paramStringScript),
          );
        }

        if (getApplicationType === 'visual-journey-script') {
          const paramStringScript = {
            fileCategory: 'visual-journey-script', //framework === 'appium' ? `appium-${language}-test-application` : 'android-test-application',
            projectName: selectProject,
            testType: 'app-automation',
          };

          dispatch(
            testExecutionActions.loadSelectVisualScriptList(paramStringScript),
          );
        }

        toast({
          title: 'Application Uploaded Successfully',
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        setApplicationType('');
        dispatch(testExecutionActions.uploadFileProgress(0));
        dispatch(testExecutionActions.appBuildApplicationFileUploaded({}));
      }
    }
  }, [fileUploadResponse]);

  useEffect(() => {
    if (
      createdPojectResponse.hasOwnProperty('message') &&
      createdPojectError !== 1
    ) {
      if (createdPojectResponse.message === 'Success') {
        toast({
          title: 'Project Created Successfully',
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        setProjectName('');
        dispatch(
          testExecutionActions.loadSelectTestExecutionProjectList(
            'app-automation',
          ),
        );
      } else {
        toast({
          title: createdPojectResponse.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }

      dispatch(testExecutionActions.testExecutionProjectCreated({}));
    }

    if (createdPojectError === 1) {
      toast({
        title: "Couldn't create the project",
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(testExecutionActions.createTestExecutionProjectError(null));
    }
  }, [createdPojectResponse, createdPojectError]);
  useEffect(() => {
    if (
      getScheduleTestResponse.hasOwnProperty('message') &&
      getScheduleTestResponseError !== 1
    ) {
      if (getScheduleTestResponse.message === 'Success') {
        toast({
          title: 'Executed/Scheduled Successfully',
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        setTimeout(() => {
          history(ROUTES.MOBILE_SESSION_SUMMARY, {
            state: { setStatusIndex: 2 },
          });
        }, 1000);

        setSelectProject('');
        setSelectAppBuild('');
        setSelectScript('');
      } else {
        toast({
          title: getScheduleTestResponse.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(testExecutionActions.testExecutionDataLoaded({}));
    }

    if (getScheduleTestResponseError === 1) {
      toast({
        title: "Couldn't Executed/Scheduled",
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(testExecutionActions.testExecutionDataError(null));
    }
  }, [getScheduleTestResponse, getScheduleTestResponseError]);

  useEffect(() => {
    if (getScheduleTestResponse.message === 'Success') {
      setSelectProject('');
      setSelectAppBuild('');
      setSelectScript('');
    }
  }, [getScheduleTestResponse]);
  useEffect(() => {
    if (selectVisualScript || selectVisualScript === '0') {
      const visualUrl = getApplicationUrlHelper(
        selectVisualScriptList,
        selectVisualScript,
      );
      setVisualUrl(visualUrl);
    }
  }, [selectVisualScript]);

  const appBuildUploadHandler = file => {
    if (selectProject === '') {
      toast({
        title: 'Please Select Project ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (framework === 'appium') {
      if (
        file.files[0].name.split('.').pop() === 'apk' ||
        file.files[0].name.split('.').pop() === 'ipa'
      ) {
      } else {
        toast({
          title: 'Please select apk or ipa file',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return;
      }
    } else {
      if (file.files[0].name.split('.').pop() != 'apk') {
        toast({
          title: 'Please select apk file',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return;
      }
    }
    getMd5SumFiles(file, setMd5Sum);
    setAppbuildFile(file.files[0]);
  };

  useEffect(() => {
    if (getMd5Sum !== '') {
      const fileType =
        appbuildFile.name.split('.').pop() === 'ipa'
          ? 'ios-application'
          : 'android-application';
      const filerequestData = {
        filename: appbuildFile.name,
        project: selectProject,
        fileCategory: framework === 'appium' ? fileType : 'android-application',
        md5: getMd5Sum,
        applicationType: 'appbuild',
        testType: 'app-automation',
      };
      dispatch(
        testExecutionActions.loadUploadApplicationAndroid(filerequestData),
      );
      setApplicationType('appbuild');
      setMd5Sum('');
    }
  }, [getMd5Sum]);

  const scriptUploadHandler = (file, fileType) => {
    if (selectProject === '') {
      toast({
        title: 'Please Select Project ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }

    if (framework === 'appium') {
      if (
        file.files[0].name.split('.').pop() != 'zip' &&
        (fileType === `appium-${language}-test-application` ||
          fileType === 'visual-journey-script')
      ) {
        toast({
          title: 'Please select zip file',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return;
      }
    } else {
      if (
        file.files[0].name.split('.').pop() != 'apk' &&
        fileType === 'android-test-application'
      ) {
        toast({
          title: 'Please select apk file',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return;
      }
      if (
        file.files[0].name.split('.').pop() != 'zip' &&
        fileType === 'visual-journey-script'
      ) {
        toast({
          title: 'Please select zip file',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return;
      }
    }
    getMd5SumFiles(file, setMd5SumScript);
    setAppbuildFile(file.files[0]);
    setApplicationType(fileType);
  };
  useEffect(() => {
    if (getMd5SumScript !== '') {
      const filerequestData = {
        filename: appbuildFile.name,
        project: selectProject,
        fileCategory: getApplicationType, //framework === 'appium' ? `appium-${language}-test-application` : 'android-test-application',
        md5: getMd5SumScript,
        applicationType: getApplicationType,
        testType: 'app-automation',
      };
      dispatch(
        testExecutionActions.loadUploadApplicationAndroid(filerequestData),
      );
      // setApplicationType('script');
      setMd5SumScript('');
    }
  }, [getMd5SumScript]);

  useEffect(() => {
    if (selectProject !== '') {
      const paramStringAppBuild = {
        fileCategory:
          framework === 'appium'
            ? 'android-application,ios-application'
            : 'android-application',
        projectName: selectProject,
        testType: 'app-automation',
      };

      dispatch(
        testExecutionActions.loadSelectAppBuildAndroidList(paramStringAppBuild),
      );
      const paramStringScript = {
        fileCategory:
          framework === 'appium'
            ? `appium-${language}-test-application`
            : 'android-test-application',
        projectName: selectProject,
        testType: 'app-automation',
      };

      dispatch(
        testExecutionActions.loadSelectScriptAndroidList(paramStringScript),
      );
      const paramStringVisualScript = {
        fileCategory: 'visual-journey-script',
        projectName: selectProject,
        testType: 'app-automation',
      };

      dispatch(
        testExecutionActions.loadSelectVisualScriptList(
          paramStringVisualScript,
        ),
      );
    }
    setSelectScript('');
    setSelectVisualScript('');
    setSelectAppBuild('');
  }, [selectProject, language]);

  useEffect(() => {
    if (
      uploadAppBuildUrlAndroid.hasOwnProperty('response') &&
      uploadAppBuildUrlAndroid.response.hasOwnProperty('message') &&
      uploadApplicationAndroidError !== 1
    ) {
      if (uploadAppBuildUrlAndroid.response.message === 'Success') {
        dispatch(
          testExecutionActions.loadUploadAppBuildFile({
            url: uploadAppBuildUrlAndroid.response.data.uploadUrl,
            file: appbuildFile,
            type: 'android-uiautomator',
            dispatch: dispatch,
          }),
          setApplicationType(uploadAppBuildUrlAndroid.type),
        );
      } else {
        toast({
          title: uploadAppBuildUrlAndroid.response.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }

      dispatch(testExecutionActions.uploadApplicationAndroidResponse({}));
    }

    if (uploadApplicationAndroidError === 1) {
      toast({
        title: 'Something went wrong',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(testExecutionActions.uploadApplicationError(null));
    }
  }, [uploadAppBuildUrlAndroid, uploadApplicationAndroidError]);

  const createProjectHandler = () => {
    if (projectName !== '') {
      const config = {
        name: projectName,
        testType: 'app-automation',
      };
      dispatch(testExecutionActions.createTestExecutionProject(config));
    } else {
      toast({
        title: 'Please Select or Create Project ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });

      return;
    }
  };

  const appBuildsPopupHandler = e => {
    setUiAutomatorMozarkTestingPopupCard(true);
    setActivePopupCard('appbuilds');
    e.preventDefault();
  };
  const testSuitePopupHandler = e => {
    setUiAutomatorMozarkTestingPopupCard(true);
    setActivePopupCard('testsuite');
    e.preventDefault();
  };

  useEffect(() => {
    if (parseInt(selectTrayValue) === 0 || parseInt(selectTrayValue) > 0) {
      setSelectedDeviceList(selectTrayOptionList[selectTrayValue].devices);
      setSelectedDeviceListUiautomator([]);
    } else if (selectTrayValue < 0) {
    } else {
      setSelectedDeviceList([]);
    }
  }, [selectTrayValue]);

  const selectedDeviceHandler = () => {
    const DeviceIds = selectedDevices.map(device => device.id);
    setSelectedDeviceList(DeviceIds);
    setActiveDevicePopup(false);
    setSelectedDeviceListUiautomator(selectedDevices);
    setTraySelectValue(-1);
    if (DeviceIds.length > 0) {
    }
  };
  const selectDeviceButtonHandler = () => {
    setActiveDevicePopup(true);

    setTimeout(() => {
      dispatch(
        testExecutionActions.addSelectedDevices(
          getSelectedDeviceListUiautomator,
        ),
      );
    }, 1000);
  };
  useEffect(() => {
    if (startDate.valueOf() + 294500 >= endDate.valueOf()) {
      setMessageFlag(true);
    } else {
      setMessageFlag(false);
    }
  }, [startDate, endDate]);

  const getOptionalParam = () => {
    let newObject = new Object();

    getAddedKey.map(item => {
      let key = '';
      let value = '';
      key = item.keyName;
      value = item.value;
      if (item.keyName && item.value) {
        const object = {
          [key.trim()]: value.trim(),
        };
        Object.assign(newObject, object);
      } else {
      }
    });

    return newObject;
  };

  useEffect(() => {
    if (
      (selectScript || selectScript === '0') &&
      selectScript !== '' &&
      selectScriptListOptions.length > 0 &&
      framework !== 'appium'
    ) {
      if (
        selectScriptListOptions[selectScript].fileParameters.hasOwnProperty(
          'testCodePackageName',
        )
      ) {
        if (
          selectScriptListOptions[selectScript].fileParameters
            .testCodePackageName === ''
        ) {
          setSelectScript('');
          toast({
            title: 'Unable to get package name/bundle ID. Upload new file',
            status: 'error',
            isClosable: true,
            position: 'bottom-right',
          });
        }
      } else {
        setSelectScript('');
        toast({
          title: 'Unable to get package name/bundle ID. Upload new file',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
    }
  }, [selectScript]);
  useEffect(() => {
    if (framework === 'appium') {
      if (selectAppBuild === '') {
        setSelectedDeviceList([]);
        setSelectedDeviceListUiautomator([]);
        setTraySelectValue(-1);
        const getAndroidIosDevice = getFilterDevice(
          getListingDevices,
          'default',
        );
        const getAndroidIosTray = getFilterDevice(
          selectTrayOptionListUiautomator,
          'default',
        );
        setFilterDevicesType(getAndroidIosDevice);
        setFilterTrayType(getAndroidIosTray);
      } else {
        setSelectedDeviceList([]);
        setSelectedDeviceListUiautomator([]);
        setTraySelectValue(-1);
        const fileCategory =
          selectAppBuildListOptions[selectAppBuild].fileCategory;
        if (fileCategory === 'android-application') {
          const getAndroidDevice = getFilterDevice(
            getListingDevices,
            'android',
          );
          const getAndroidTray = getFilterDevice(
            selectTrayOptionListUiautomator,
            'android',
          );
          setFilterDevicesType(getAndroidDevice);
          setFilterTrayType(getAndroidTray);
        } else {
          const getIosDevice = getFilterDevice(getListingDevices, 'ios');
          const getIosTray = getFilterDevice(
            selectTrayOptionListUiautomator,
            'ios',
          );
          setFilterDevicesType(getIosDevice);
          setFilterTrayType(getIosTray);
        }
      }
    }
  }, [selectAppBuild]);

  const scheduleAutomationTestHandler = scheduleType => {
    if (selectProject === '') {
      toast({
        title: 'Please Select Project ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (selectAppBuild === '') {
    }
    if (selectScript === '') {
      toast({
        title: 'Please Select Script ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (!selectVisualScript && isVisEnabled) {
      toast({
        title: 'Please Visual Select Script ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (getSelectedDeviceList.length <= 0) {
      toast({
        title: 'Please Select  Devices',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (
      startDate.valueOf() + 294500 >= endDate.valueOf() &&
      scheduleType === 'SCHEDULE'
    ) {
      toast({
        title: 'Start & End time have Invalid Interval',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (
      parseInt(getInterval) < parseInt(getMaxTestDuration) &&
      scheduleType === 'SCHEDULE'
    ) {
      toast({
        title: 'Invalid Max Test Duration > Interval',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }

    if (
      (endDate.valueOf() - startDate.valueOf()) / 60000 < getInterval &&
      scheduleType === 'SCHEDULE'
    ) {
      toast({
        title: 'Invalid time interval',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }

    if (
      startDate.valueOf() <= new Date().valueOf() &&
      scheduleType === 'SCHEDULE'
    ) {
      toast({
        title: 'Start and End Time Invalid',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    const optionValue = getOptionalParam();

    const devicebusy = validateBusyDevice(
      busyDeviceSlots,
      new Date(dateTimeConverterWithTimeZone(startDate)).getTime(),
      new Date(dateTimeConverterWithTimeZone(endDate)).getTime(),
    );

    if (devicebusy.length > 0) {
      toast({
        title: 'Busy Device Occured',
        description: devicebusy.join(' , '),
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }

    const config = {
      deviceId: getSelectedDeviceList,
      testConfiguration: {
        captureHAR: recordNetworkCalls,
        captureCPUMetrics: recordCPU,
        captureMemoryMetrics: recordMemory,
        captureBatteryMetrics: recordBattery,
        captureGraphicsMetrics: recordGraphics,
        captureDeviceScreenShots: recordDeviceScreenShots,
        recordDeviceScreen: isVisEnabled ? isVisEnabled : recordDeviceScreen,
        captureDeviceNetworkPackets: recordDeviceNetworkPackets,
        captureAutomationLogs: systemDebugLogs,
        captureSystemDebugLogs: systemDebugLogs,
        captureLiveLogs: liveLogs,
      },
      scheduleConfiguration:
        scheduleType === 'SCHEDULE'
          ? {
              startTime: dateTimeConverterWithTimeZone(startDate),
              endTime: dateTimeConverterWithTimeZone(endDate),
              interval: parseInt(getInterval),
            }
          : {
              startTime: dateTimeConverterWithTimeZone(new Date()),
            },
      testAction: {
        pre: {},
        post: {},
      },
      testParameters: {
        testType: 'app-automation',
        maxTestDuration: parseInt(getMaxTestDuration),
        testFramework:
          framework === 'appium' ? `appium-${language}` : 'android-uiautomator',
        projectName: selectProject,
        packageName: getPackageName,
        browserName:
          framework === 'appium' && getActiveTab === 1 ? selectBrowser : '',
        browserVersion:
          framework === 'appium' && getActiveTab === 1 ? selectVersion : '',
        trayName: getTrayFromList(selectTrayValue, selectTrayOptionList),
        visualMonitoringEnabled: isVisEnabled,
        visualTestApplicationUrl: isVisEnabled
          ? getApplicationUrlHelper(selectVisualScriptList, selectVisualScript)
          : '',
        ...optionValue,
      },
      applicationUrl:
        getActiveTab === 0
          ? getApplicationUrlHelper(selectAppBuildListOptions, selectAppBuild)
          : '',
      testApplicationUrl: getApplicationUrlHelper(
        selectScriptListOptions,
        selectScript,
      ),
      executionType: scheduleType,
    };
    // console.log({config})
    dispatch(testExecutionActions.loadTestExecutionData(config));
  };
  const addKeyHandler = () => {
    setAddedKey([...getAddedKey, { keyName: ' ', value: ' ' }]);
  };

  const refreshApplicationList = fileCategory => {
    if (selectProject === '') {
      toast({
        title: 'Please Select Project ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (selectProject !== '') {
      if (
        fileCategory === 'android-application' ||
        fileCategory === 'android-application,ios-application'
      ) {
        const paramStringAppBuild = {
          fileCategory:
            framework === 'appium'
              ? 'android-application,ios-application'
              : 'android-application',
          projectName: selectProject,
          testType: 'app-automation',
        };

        dispatch(
          testExecutionActions.loadSelectAppBuildAndroidList(
            paramStringAppBuild,
          ),
        );
        setSelectAppBuild('');
      } else if (
        fileCategory === 'android-test-application' ||
        fileCategory === `appium-${language}-test-application`
      ) {
        const paramStringScript = {
          fileCategory:
            framework === 'appium'
              ? `appium-${language}-test-application`
              : 'android-test-application',
          projectName: selectProject,
        };

        dispatch(
          testExecutionActions.loadSelectScriptAndroidList(paramStringScript),
        );
        setSelectScript('');
      } else if (fileCategory === 'visual-journey-script') {
        const paramStringVisualScript = {
          fileCategory: 'visual-journey-script',
          projectName: selectProject,
          testType: 'app-automation',
        };

        dispatch(
          testExecutionActions.loadSelectVisualScriptList(
            paramStringVisualScript,
          ),
        );
        setSelectVisualScript('');
      }
    }
  };
  console.log({ selectedDevices });
  const browserList = [{ name: 'chrome' }];
  const versionList = [{ name: '1.128.0' }];
  return (
    <>
      <Flex flexDirection="column" position="relative">
        {(loadingCreateProject || loadingExecuteSchedule) && (
          <LoadingComponent
            height="120vh"
            top="-65px"
            isOverLay={true}
            position="absolute"
          ></LoadingComponent>
        )}
        <Stack spacing="10px" padding="15px 25px">
          <LabelFieldComponent label="Project">
            <SelectComponent
              name="uiautomator"
              type="project"
              selectValue={selectProject}
              setSelectValue={setSelectProject}
              selectOptions={selectProjectOptionList}
              title="Select Project"
            ></SelectComponent>
            <Text padding="0px 5px">OR</Text>
            <InputComponent
              setInputValue={setProjectName}
              inputValue={projectName}
              title="Enter Project Name"
            ></InputComponent>
            <ButtonComp
              name="createProjectUiautomator"
              padding="7px 12px"
              buttonHandler={createProjectHandler}
            >
              Create
            </ButtonComp>
          </LabelFieldComponent>
          {framework === 'appium' && (
            <AppBuildMwebButton
              setActiveTab={setActiveTab}
              appbuild={
                <LabelFieldComponent label="App Build">
                  <SelectComponent
                    type="application"
                    name="uiautomator"
                    selectOptions={selectAppBuildListOptions}
                    selectValue={selectAppBuild}
                    setSelectValue={setSelectAppBuild}
                    title="Default"
                  ></SelectComponent>

                  <RefreshIconButton
                    clickHandler={() =>
                      refreshApplicationList(
                        framework === 'appium'
                          ? 'android-application,ios-application'
                          : 'android-application',
                      )
                    }
                  ></RefreshIconButton>
                  <Text padding="0px 5px">OR</Text>
                  <UploadButton
                    validate={framework === 'appium' ? '.ipa , .apk' : '.apk'}
                    placeholder="Upload App Build"
                    uploadButtonHandler={appBuildUploadHandler}
                    enableProgress={
                      getApplicationType === 'appbuild' &&
                      (loadingFileUpload || loadingapplicationUrl)
                    }
                    progressValue={getUploadProgessValue}
                  ></UploadButton>
                </LabelFieldComponent>
              }
              mweb={
                <LabelFieldComponent label="Browser & Version">
                  <SelectComponent
                    type="project"
                    name="uiautomator"
                    selectOptions={browserList}
                    selectValue={selectBrowser}
                    setSelectValue={setSelectBrowser}
                    title="Default"
                  ></SelectComponent>
                  <SelectComponent
                    type="project"
                    name="uiautomator"
                    selectOptions={versionList}
                    selectValue={selectVersion}
                    setSelectValue={setSelectVersion}
                    title="Default"
                  ></SelectComponent>
                </LabelFieldComponent>
              }
            ></AppBuildMwebButton>
          )}
          {framework === 'uiautomator' && (
            <LabelFieldComponent label="App Build">
              <SelectComponent
                type="application"
                name="uiautomator"
                selectOptions={selectAppBuildListOptions}
                selectValue={selectAppBuild}
                setSelectValue={setSelectAppBuild}
                title="Default"
              ></SelectComponent>

              <RefreshIconButton
                clickHandler={() =>
                  refreshApplicationList(
                    framework === 'appium'
                      ? 'android-application,ios-application'
                      : 'android-application',
                  )
                }
              ></RefreshIconButton>
              <Text padding="0px 5px">OR</Text>
              <UploadButton
                validate={framework === 'appium' ? '.ipa , .apk' : '.apk'}
                placeholder="Upload App Build"
                uploadButtonHandler={appBuildUploadHandler}
                enableProgress={
                  getApplicationType === 'appbuild' &&
                  (loadingFileUpload || loadingapplicationUrl)
                }
                progressValue={getUploadProgessValue}
              ></UploadButton>
            </LabelFieldComponent>
          )}
          <>
            <LabelFieldComponent label="Test Suite">
              <SelectComponent
                name="uiautomator"
                selectOptions={selectScriptListOptions}
                title="Select Test Suite"
                selectValue={selectScript}
                setSelectValue={setSelectScript}
              ></SelectComponent>

              <RefreshIconButton
                clickHandler={() =>
                  refreshApplicationList(
                    framework === 'appium'
                      ? `appium-${language}-test-application`
                      : 'android-test-application',
                  )
                }
              ></RefreshIconButton>
              <Text padding="0px 5px">OR</Text>

              <UploadButton
                validate={framework === 'appium' ? '.zip' : '.apk'}
                uploadButtonHandler={scriptUploadHandler}
                placeholder="Upload Test Suite"
                fileType={
                  framework === 'appium'
                    ? `appium-${language}-test-application`
                    : 'android-test-application'
                }
                enableProgress={
                  (getApplicationType ===
                    `appium-${language}-test-application` ||
                    getApplicationType === 'android-test-application') &&
                  (loadingapplicationUrl || loadingFileUpload)
                }
                progressValue={getUploadProgessValue}
              ></UploadButton>
              <Text>Enable Visual Monitoring</Text>
              <Switch
                size="md"
                onChange={e => setVisEnable(e.target.checked)}
              />
            </LabelFieldComponent>
          </>
          {isVisEnabled && (
            <LabelFieldComponent label="Visual Journey Script">
              <SelectComponent
                name="xcui"
                type="application"
                selectOptions={selectVisualScriptList}
                selectValue={selectVisualScript}
                setSelectValue={setSelectVisualScript}
                title="Select Visual Journey"
              ></SelectComponent>

              <RefreshIconButton
                clickHandler={() =>
                  refreshApplicationList('visual-journey-script')
                }
              ></RefreshIconButton>
              <Text padding="0px 5px">OR</Text>

              <UploadButton
                validate=".zip"
                fileType="visual-journey-script"
                uploadButtonHandler={scriptUploadHandler}
                enableProgress={
                  getApplicationType === 'visual-journey-script' &&
                  (loadingapplicationUrl || loadingFileUpload)
                }
                progressValue={getUploadProgessValue}
                placeholder="Upload Visual Journey"
              ></UploadButton>
            </LabelFieldComponent>
          )}
          <LabelFieldComponent label="Devices">
            <Flex h="39px">
              <CustomIconButton>android</CustomIconButton>
              {framework === 'appium' && (
                <CustomIconButton>ios</CustomIconButton>
              )}
            </Flex>
            <DashedButton
              height="39px"
              width="250px"
              name="selectDeviceUiautomator"
              buttonHandler={selectDeviceButtonHandler}
            >
              Select Device(<Text textTransform="lowercase">s</Text>)
            </DashedButton>
            <Text>OR</Text>
            <SelectTrayOption
              selectOptions={
                framework === 'appium'
                  ? getFilterTrayType
                  : selectTrayOptionList
              }
              title="Select Tray"
              selectValue={selectTrayValue}
              setSelectValue={setTraySelectValue}
            ></SelectTrayOption>

            {getSelectedDeviceList.length && (
              <Text>{getSelectedDeviceList.length} Device selected</Text>
            )}
          </LabelFieldComponent>
          <SelectDevicePopup
            selectedDeviceHandler={selectedDeviceHandler}
            selectedDeviceList={selectedDevices}
            tableData={
              framework === 'appium'
                ? getFilterDeviceType
                : getAndroidDevicesList
            }
            setActiveDevicePopup={setActiveDevicePopup}
            activeDevicePopup={activeDevicePopup}
            isLoading={deviceListListLoading}
            errorDeviceData={deviceListDataError}
          ></SelectDevicePopup>

          <Flex marginTop="10px" align="flex-start">
            <Flex
              width="250px"
              align="center"
              height="35px"
              textTransform="capitalize"
            >
              <Text whiteSpace="nowrap">Record Test</Text>
            </Flex>
            <Flex flex={1} flexDirection="column">
              <HStack spacing="0px">
                <CheckboxComp
                  value={recordDeviceScreen || isVisEnabled}
                  title="Screen Recording"
                  setCheckedValue={setRecordDeviceScreen}
                ></CheckboxComp>
                <CheckboxComp
                  value={systemDebugLogs}
                  title="Automation & Debug Logs"
                  setCheckedValue={setSystemDebugLogs}
                ></CheckboxComp>
              </HStack>

              <HStack spacing="0px"></HStack>
              <HStack spacing="0px">
                <CheckboxComp
                  value={recordNetworkCalls}
                  title="Network Logs"
                  setCheckedValue={setRecordNetworkCalls}
                ></CheckboxComp>
              </HStack>
              <HStack spacing="0px"></HStack>
            </Flex>
          </Flex>
          <LabelFieldComponent>
            <Text maxWidth="45%">
              Test Parameters
              <InfoComponent
                labelmessage={`Max Test Duration ${getMaxTestDuration} min`}
              ></InfoComponent>{' '}
            </Text>
            <LaunchIconButton
              viewCode={testSuitePopupHandler}
            ></LaunchIconButton>
          </LabelFieldComponent>
          <LabelFieldComponent label="Execute Test">
            <ButtonComp
              isDisabled={!getSelectedDeviceList.length || !areDevicesBusy}
              fontWeight="500"
              name="executeNowUiautomator"
              padding="7px 20px"
              backgroundColor="blue1"
              color="white"
              buttonHandler={() => scheduleAutomationTestHandler('NOW')}
            >
              Now
            </ButtonComp>
          </LabelFieldComponent>
          <LabelFieldComponent>
            <Text padding="5px">OR</Text>
          </LabelFieldComponent>
          <LabelFieldComponent>
            <ButtonComp
              isDisabled={!getSelectedDeviceList.length}
              fontWeight="500"
              padding="7px 20px"
              backgroundColor="blue1"
              color="white"
              marginLeft="0px"
              buttonHandler={() => scheduleAutomationTestHandler('SCHEDULE')}
              name="scheduleTestUiautomator"
            >
              Schedule
            </ButtonComp>
          </LabelFieldComponent>

          <LabelFieldComponent>
            <Text padding="5px" textTransform="none" width="200px">
              Start date and time
              <InfoComponent labelmessage="Local time zone"></InfoComponent>
            </Text>
            <InputDateTime
              name="startDateUiautomator"
              selectedDate={startDate}
              setSelectDate={setStartDate}
            ></InputDateTime>
          </LabelFieldComponent>

          <LabelFieldComponent>
            <Text padding="5px" textTransform="none" width="200px">
              End date and time
              <InfoComponent labelmessage="Local time zone"></InfoComponent>
            </Text>
            <InputDateTime
              name="endDateUiautomator"
              selectedDate={endDate}
              setSelectDate={setEndDate}
            ></InputDateTime>
            {getMessageFlag && (
              <Text color="red1" textTransform="none">
                {' '}
                *End time &gt; Start time{' '}
              </Text>
            )}
          </LabelFieldComponent>
          <LabelFieldComponent>
            <Text padding="5px" width="200px">
              Interval
              <InfoComponent labelmessage="Interval between two test execution a single device(in minutes)"></InfoComponent>
            </Text>
            <ToggleSelectInput
              isShadow={true}
              selectOptions={timeIntervalValue}
              selectValue={getInterval}
              setSelectValue={setInterval}
            ></ToggleSelectInput>
          </LabelFieldComponent>

          <PopupCardDetail
            isOpen={uiAutomatorMozarkTestingPopupCard}
            onClose={setUiAutomatorMozarkTestingPopupCard}
          >
            <Stack
              width="100%"
              padding="10px 5px"
              spacing="15px"
              overflowY="auto"
              css={{
                '&::-webkit-scrollbar': {
                  width: '8px',
                  border: '5px solid #F3F6F9',
                },
                '&::-webkit-scrollbar-track': {
                  boxShadow: 'inset 0 0 6px white',
                },
                '&::-webkit-scrollbar-thumb': {
                  backgroundColor: '#C1C1C1',
                  borderRadius: '5px',
                  boreder: '2px solid',
                },
              }}
            >
              <KeyValueComponent
                label="Project Name"
                getValue={selectProject}
                isDisabled={true}
              ></KeyValueComponent>
              <KeyValueComponent
                label="Test Framework"
                getValue={
                  framework ? `appium-${language}` : 'android-uiautomator'
                }
                isDisabled={true}
              ></KeyValueComponent>
              {selectAppBuild === '' && (
                <KeyValueComponent
                  isMandatory={false}
                  label="Package Name"
                  getValue={getPackageName}
                  setValue={setPackageName}
                  isDisabled={false}
                ></KeyValueComponent>
              )}

              <Flex justifyContent="space-between">
                <Text>Maximum Test Duration</Text>

                <SelectValueComponent
                  isShadow={false}
                  width="150px"
                  selectOptions={timeIntervalValue}
                  selectValue={getMaxTestDuration}
                  setSelectValue={setMaxTestDuration}
                ></SelectValueComponent>
              </Flex>
              {isVisEnabled && (
                <Flex justifyContent="space-between">
                  <Text>Visual Synthetic Monitoring</Text>
                  <Switch isChecked={isVisEnabled} />
                </Flex>
              )}
              {isVisEnabled && (
                <KeyValueComponent
                  isMandatory={false}
                  isDisabled={true}
                  label="Visual Journey Script"
                  getValue={isVisEnabled ? getVisualUrl : ''}
                  // setValue={setPackageName}
                ></KeyValueComponent>
              )}
              {getAddedKey.length > 0 &&
                getAddedKey.map((item, index) => (
                  <NewKeyValueComponent
                    objectData={item}
                    key={index}
                  ></NewKeyValueComponent>
                ))}
              <Flex justifyContent="flex-end">
                <ButtonComp
                  fontSize="18px"
                  padding="5px 10px"
                  backgroundColor="blue1"
                  color="white"
                  margin="0px"
                  fontWeight="600"
                  buttonHandler={addKeyHandler}
                  name="addNewParamUiautomator"
                >
                  +
                </ButtonComp>
              </Flex>

              <Flex justify="center">
                <ButtonComp
                  fontWeight="500"
                  padding="7px 25px"
                  backgroundColor="blue1"
                  color="white"
                  margin="0px"
                  name="saveOptionParamUiautomator"
                  buttonHandler={() =>
                    setUiAutomatorMozarkTestingPopupCard(false)
                  }
                >
                  Save
                </ButtonComp>
              </Flex>
            </Stack>
          </PopupCardDetail>
        </Stack>
      </Flex>
    </>
  );
}

export default UiAutomatorMozarkTesting;
