// @ts-nocheck
import { Flex, Stack, Text, useToast } from '@chakra-ui/react';
import DashedButton from 'app/components/DashedButton';
import CustomIconButton from 'app/pages/Appium/components/CustomIconButton';
import SelectDevicePopup from 'app/pages/Appium/components/SelectDevicePopup';
import LabelFieldComponent from 'app/pages/AutomationTest/components/LabelFieldComponent';
import { selecteTrayList } from 'app/pages/MyDevices/slice/selector';
import SelectTrayOption from 'app/pages/UiAutomator/components/SelectTrayOption';
import { useTestExecutionSlice } from 'app/pages/Xcui/slice';
import setHours from 'date-fns/setHours';
import setMinutes from 'date-fns/setMinutes';
import {
  selectListingDevices,
  selectTestExecutionSelectedDevice,
} from 'app/pages/Xcui/slice/selector';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ButtonComp from 'app/components/ButtonComp';
import InfoComponent from 'app/pages/Appium/components/InfoComponent';
import InputDateTime from 'app/pages/Appium/components/InputDateTime';
import getTrayFromList from 'utils/getTrayFromList';
import ToggleSelectInput from 'app/pages/UiAutomator/components/ToggleSelectInput';
import { timeIntervalValue } from 'app/pages/Xcui/Features/XcUiMozarkTesting';
import { dateTimeConverterWithTimeZone } from 'utils/gernricfunction';

function LocalTestingFeatures() {
  const [getMessageFlag, setMessageFlag] = useState(false);
  const [getMaxTestTime, setMaxTestTime] = useState(5);
  const [activeDevicePopup, setActiveDevicePopup] = useState(false);
  const [getSelectedDeviceListLocalTesting, setSelectedDeviceListLocalTesting] =
    useState([]);
  const [getSelectedDeviceList, setSelectedDeviceList] = useState([]);
  const getListingDevices = useSelector(selectListingDevices);
  const selectedDevices = useSelector(selectTestExecutionSelectedDevice);
  const selectTrayOptionList = useSelector(selecteTrayList);
  const [selectTrayValue, setTraySelectValue] = useState();
  const toast = useToast();
  const [startDate, setStartDate] = useState(
    setHours(
      setMinutes(new Date(), new Date().getMinutes()),
      new Date().getHours(),
    ),
  );
  const [endDate, setEndDate] = useState(
    setHours(
      setMinutes(new Date(), new Date().getMinutes()),
      new Date().getHours(),
    ),
  );

  const getAndroidDevicesList = getListingDevices.filter(
    device => device.platform === 'android' || device.platform === 'ios',
  );
  const dispatch = useDispatch();

  const { testExecutionActions } = useTestExecutionSlice();

  useEffect(() => {
    if (parseInt(selectTrayValue) === 0 || parseInt(selectTrayValue) > 0) {
      setSelectedDeviceList(selectTrayOptionList[selectTrayValue].devices);
      setSelectedDeviceListLocalTesting([]);
    } else if (selectTrayValue < 0) {
    } else {
      setSelectedDeviceList([]);
    }
  }, [selectTrayValue]);

  const selectedDeviceHandler = () => {
    const DeviceIds = selectedDevices.map(device => device.id);
    setSelectedDeviceList(DeviceIds);
    setActiveDevicePopup(false);
    setSelectedDeviceListLocalTesting(selectedDevices);
    setTraySelectValue(-1);
    if (DeviceIds.length > 0) {
    }
  };
  const selectDeviceButtonHandler = () => {
    setActiveDevicePopup(true);

    setTimeout(() => {
      dispatch(
        testExecutionActions.addSelectedDevices(
          getSelectedDeviceListLocalTesting,
        ),
      );
    }, 1000);
  };

  const scheduleAutomationTestHandler = scheduleType => {
    if (getSelectedDeviceList.length <= 0) {
      toast({
        title: 'Please Select  Devices',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (getMaxTestTime <= 0) {
      toast({
        title: 'Invalid Max Test Duration',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }

    if (
      startDate.valueOf() <= new Date().valueOf() &&
      scheduleType === 'SCHEDULE'
    ) {
      toast({
        title: 'Start Time Invalid',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    const optionValue = {}; // getOptionalParam();

    const config = {
      deviceId: getSelectedDeviceList,
      testConfiguration: {
        captureHAR: false,
        captureCPUMetrics: false,
        captureMemoryMetrics: false,
        captureBatteryMetrics: false,
        captureGraphicsMetrics: false,
        captureDeviceScreenShots: false,
        recordDeviceScreen: false,
        captureDeviceNetworkPackets: false,
        captureAutomationLogs: false,
        captureSystemDebugLogs: false,
        captureLiveLogs: false,
      },
      scheduleConfiguration:
        scheduleType === 'SCHEDULE'
          ? {
              startTime: dateTimeConverterWithTimeZone(startDate),
              endTime: dateTimeConverterWithTimeZone(startDate),
              interval: 10, // parseInt(getMaxTestTime),
            }
          : {
              startTime: dateTimeConverterWithTimeZone(new Date()),
            },
      testAction: {
        pre: {},
        post: {},
      },
      testParameters: {
        testType: 'local-testing',
        maxTestDuration: Number(getMaxTestTime),
        testFramework: '',
        projectName: '',
        packageName: '',
        trayName: getTrayFromList(selectTrayValue, selectTrayOptionList),
        ...optionValue,
      },
      applicationUrl: '',
      testApplicationUrl: '',
      executionType: scheduleType,
    };
    dispatch(testExecutionActions.loadTestExecutionData(config));
  };
  const viewUrlListHandler = () => {};

  return (
    <Flex
      flexDirection="column"
      fontFamily="Montserrat"
      fontWeight="500"
      fontSize="14px"
      color="grey13"
      bg="white"
      width="100%"
    >
      <Stack spacing="20px" padding="15px 25px">
        <LabelFieldComponent label="Devices">
          <Flex h="39px">
            <CustomIconButton>android</CustomIconButton>
            <CustomIconButton>ios</CustomIconButton>
          </Flex>
          <DashedButton
            height="39px"
            width="250px"
            name="selectDeviceLocalTesting"
            buttonHandler={selectDeviceButtonHandler}
          >
            Select Device(<Text textTransform="lowercase">s</Text>)
          </DashedButton>
          <Text>OR</Text>
          <SelectTrayOption
            selectOptions={selectTrayOptionList}
            title="Select Tray"
            selectValue={selectTrayValue}
            setSelectValue={setTraySelectValue}
          ></SelectTrayOption>
          {getSelectedDeviceList.length && (
            <Text>{getSelectedDeviceList.length} Device selected</Text>
          )}
        </LabelFieldComponent>
        <SelectDevicePopup
          deviceOs="ios"
          selectedDeviceHandler={selectedDeviceHandler}
          tableData={getAndroidDevicesList}
          setActiveDevicePopup={setActiveDevicePopup}
          activeDevicePopup={activeDevicePopup}
          selectedDeviceList={selectedDevices}
          isLoading={false}
          errorDeviceData={null}
        ></SelectDevicePopup>
        <LabelFieldComponent label="Execute Test">
          <ButtonComp
            name="executeNowXcui"
            fontWeight="500"
            padding="7px 20px"
            backgroundColor="blue1"
            color="white"
            buttonHandler={() => scheduleAutomationTestHandler('NOW')}
          >
            Now
          </ButtonComp>
        </LabelFieldComponent>
        <LabelFieldComponent>
          <Text>OR</Text>
        </LabelFieldComponent>
        <LabelFieldComponent label="">
          <ButtonComp
            name="scheduleTestXcui"
            fontWeight="500"
            padding="7px 20px"
            backgroundColor="blue1"
            color="white"
            marginLeft="0px"
            buttonHandler={() => scheduleAutomationTestHandler('SCHEDULE')}
          >
            SCHEDULE
          </ButtonComp>
        </LabelFieldComponent>

        <LabelFieldComponent>
          <Text padding="5px" textTransform="none" width="200px">
            Start date and time
            <InfoComponent labelmessage="Local time zone"></InfoComponent>
          </Text>
          <InputDateTime
            name="startDateXcui"
            selectedDate={startDate}
            setSelectDate={setStartDate}
          ></InputDateTime>
        </LabelFieldComponent>

        <LabelFieldComponent>
          <Text padding="5px" width="200px">
            Max Test Duration
            <InfoComponent labelmessage="Max Test duration in minutes"></InfoComponent>
          </Text>
          <ToggleSelectInput
            isShadow={true}
            selectOptions={timeIntervalValue}
            selectValue={getMaxTestTime}
            setSelectValue={setMaxTestTime}
          ></ToggleSelectInput>
        </LabelFieldComponent>
      </Stack>
    </Flex>
  );
}

export default LocalTestingFeatures;
