// @ts-nocheck
import {
  Flex,
  HStack,
  Stack,
  Tab,
  TabList,
  TabPanel,
  TabPanels,
  Tabs,
  Text,
  useRadioGroup,
} from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import SubContainer from 'app/components/SubContainer';
import SubLayout from 'app/components/SubLayout';
import React, { useState } from 'react';
import LabelFieldComponent from '../AutomationTest/components/LabelFieldComponent';
import UiAutomatorMozarkTesting from '../UiAutomator/Features/UiAutomatorMozarkTesting';
import CustomTabButton from './components/CustomTabButton';

import SelectButton from './components/SelectButton';
import AppMobileMode from './Features/AppMobileMode';
import AppMobile from './Features/AppMobileMode';
import CiIntegration from './Features/CiIntegration';
import LocalTesting from './Features/LocalTesting';
import MozarkTesting from './Features/MozarkTesting';
import LocalTestingFeatures from '../LocalTesting/Features/LocalTestingFeatures';

export function Appium({ getTabIndex }) {
  const [selectedLanguage, setSetselectedLanguage] = useState('python');

  const options = ['python', 'java'];

  const { getRootProps, getRadioProps } = useRadioGroup({
    name: 'language',
    defaultValue: 'python',
    onChange: val => setSetselectedLanguage(val),
  });

  const group = getRootProps();

  return (
    <SubLayout>
      <Flex
        flexDirection="column"
        fontFamily="Montserrat"
        fontWeight="500"
        fontSize="14px"
        color="grey13"
        bg="white"
        width="100%"
        // padding="15px 25px"
      >
        <Tabs width="100%">
          <Stack spacing="10px">
            <Flex marginLeft="25px">
              <LabelFieldComponent label="Execution Type">
                <CustomTabButton>Mozark Testing</CustomTabButton>
                <CustomTabButton>Hub Testing</CustomTabButton>
              </LabelFieldComponent>
            </Flex>

            <HStack width="100%">
              <TabPanels padding="0px" margin="0px">
                <TabPanel width="100%" padding="0px">
                  <Flex paddingLeft="27px">
                    <LabelFieldComponent label="Language">
                      <HStack {...group}>
                        {options.map(value => {
                          const radio = getRadioProps({ value });
                          return (
                            <SelectButton key={value} {...radio}>
                              {value}
                            </SelectButton>
                          );
                        })}
                      </HStack>
                    </LabelFieldComponent>
                  </Flex>
                  <UiAutomatorMozarkTesting
                    getTabIndex={getTabIndex}
                    framework="appium"
                    language={selectedLanguage}
                  ></UiAutomatorMozarkTesting>
                </TabPanel>
                <TabPanel width="100%" padding="0px">
                  <LocalTestingFeatures></LocalTestingFeatures>
                </TabPanel>
              </TabPanels>
            </HStack>
          </Stack>
        </Tabs>
      </Flex>
    </SubLayout>
  );
}

export default Appium;
